export default {
	name: 'index',
	data() {
		return {
			autoplay: true,
			interval: 1000,
			duration: 500,
			vertical: true,
			swiperList: 5,
			isShowNoitce: false
		}
	},
	props:{
		isHome: { //判断是否是首页的通告,  首页通告样式有些区别
			type: Boolean,
			default: false
		},
		isLottery: {
			type: Boolean,
			default: false
		}
	},
	mounted() {
		this.$nextTick(function(){
			if(this.isLottery){
				this.getLotteryData();
			}else{
				this.getData();
			}
		})
	},
	methods: {
		getData(){
			this.$http.get('/bargain_v1/bargain/success-list')
				.then(res => {
					console.log(res.data);
					let data = res.data.data;
					if(res.data.code == 1){
						if(data.length > 0){
							this.isShowNoitce = true;
							this.swiperList = data;
						}
					}
				}).catch(err => {
					console.log(err);
				})
		},
		getLotteryData(){
			this.$http.get('/bargain_v1/envelope/list',{
				params:{
					token: this.$store.getters.token
				}
			})
				.then(res => {
					console.log(res.data);
					let data = res.data.data;
					if(res.data.code == 1){
						if(data.length > 0){
							this.isShowNoitce = true;
							this.swiperList = data;
						}
					}
				}).catch(err => {
					console.log(err);
				})
		}
	}
}
